html, body {
  margin: 0px;
  padding: 0px;
  height: 100%;
  width: 100%;
  font-family: 'Lato', sans-serif !important; }

a {
  -webkit-transition: all 0.4s ease-in-out !important;
  -o-transition: all 0.4s ease-in-out !important;
  transition: all 0.4s ease-in-out !important; }

.nav {
  background-color: #037Ef3; }
  .nav .logo {
    padding: 0;
    margin: 12px 0px; }
    .nav .logo img {
      width: 180px;
      line-height: 60px; }
  .nav .nav-links {
    margin: 12px 0px;
    text-align: right; }
    .nav .nav-links ul {
      margin-bottom: 0; }
      .nav .nav-links ul a {
        color: white; }
      .nav .nav-links ul li:nth-child(3) a {
        color: black !important; }

.hero {
  height: 100%;
  width: auto;
  background-image: url("lib/img/blue-hex.jpg");
  background-position: center;
  background-size: cover; }
  .hero .hero-container {
    padding-left: 0; }
  .hero .hero-text {
    padding: 80px 0px;
    padding-right: 65%;
    color: white; }
    .hero .hero-text h1 {
      font-size: 64px; }
    .hero .hero-text .about-us {
      display: block;
      margin-bottom: 4px;
      color: white;
      font-size: 25px; }
      .hero .hero-text .about-us:hover {
        text-decoration: none;
        border-bottom: solid 2px #037Ef3; }
    .hero .hero-text p {
      font-size: 16px; }
    .hero .hero-text .btn-getstarted {
      color: white;
      border-color: #037Ef3;
      background-color: #037Ef3; }
      .hero .hero-text .btn-getstarted:hover {
        background-color: transparent; }

.contents .tour-packages h3 {
  text-align: center;
  font-style: italic; }

.contents .tour-packages .tour-package {
  margin-bottom: 50px;
  padding: 20px 10px;
  text-align: center;
  border-radius: 2px;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: box-shadow .6s,-webkit-box-shadow .6s; }
  .contents .tour-packages .tour-package img {
    width: 240px;
    height: 80px;
    display: inline; }
  .contents .tour-packages .tour-package p {
    font-size: 16px; }
  .contents .tour-packages .tour-package h5 {
    font-size: 12px;
    text-transform: uppercase;
    color: gray; }
  .contents .tour-packages .tour-package a {
    display: block;
    color: gray;
    font-size: 18px; }
  .contents .tour-packages .tour-package:hover {
    -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22); }

.contents .tour-packages .orange-package {
  border-top: solid 5px #F85A40; }

.contents .tour-packages .blue-package {
  border-top: solid 5px #0A8EA0; }

.contents .tour-packages .green-package {
  border-top: solid 5px #30C39E; }
